.App {
  
}

body{
  background: #0b0c13;
}

#name-input{
  position: absolute;
  top: -30px;
  user-select: none;
  cursor: default;
}

input:focus{
  outline: none;
}

.champ-name{
  margin: 0;
  background-size: cover;
  background-position: center top;
  display: flex;
  transition: min-height .5s ease-in-out;
}
.champ-name div{
  width: 100%;
  color: gold;
  font-size: 100px;
  font-family: 'BeaufortforLOL-Bold';
  text-align: center;
  align-self: flex-end;
}

.champ-tagline{
  font-family: 'BeaufortforLOL-Bold';
  color: white;
  margin: 0 0 50px 0;
  text-align: center;
}

.champ-info-box{
  display: flex;
  width:90%;
  max-width: 1200px;
  margin: auto;
  padding-bottom: 00px;
}

.champ-stats, .champ-desc{
  color: white;
  width: 50%;
  padding: 30px;
}

.champ-stats{
  text-align: right;
}

.read-more{
  padding: 0;
  background: none;
  color: gold;
  text-decoration: underline;
  cursor: pointer;
  border: none;
}
.read-more:hover{
  color: rgb(255, 115, 0);
}

@media (max-width: 750px){
  .champ-info-box{
    display: block;
  }

  .champ-stats, .champ-desc{
    width: unset;
  }
}